import React from 'react'
import { graphql } from 'gatsby'

import { Layout } from '../layout'
import { Head } from '../components/head'

class NotFoundPage extends React.Component {
  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title

    console.log('test = ', data.site.siteMetadata)

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <Head title="404: Not Found" />

        <h1>페이지를 찾을 수 없어요...</h1>

        <h2>불편을 드려 죄송합니다.</h2>
        <p>
          이전 게시글(Hexo 블로그시절)의 URL이 꼬여서 찾을 수 없는 경우가
          있습니다.
        </p>

        <p>
          만약 꼭 필요한 자료라면 불편하시겠지만... <br></br>메인 페이지에서
          키워드 검색을 해주시면 대단히 감사하겠습니다 ^^
        </p>
        <p>방문해 주셔서 감사하고 좋은 하루 보내세요 ^^</p>

        <br></br>
        <center>
          <img src={'../../thumbnail005.png'} width="960px" />
          <br></br>
          Photo by <a href="http://instagram.com/deprecated0_0/">MHLab</a>
        </center>
      </Layout>
    )
  }
}

export default NotFoundPage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
